import React, { useEffect, useRef, useState, forwardRef } from 'react';

import SimpleForm from '~app-src/component/SimpleForm';
import SimpleTable from '~app-src/component/SimpleTable';
import Spacer from '~app-src/component/Spacer';

import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';

import Paper from "@material-ui/core/Paper";
import ProformaRequestForm from '~app-src/module/proforma/ProformaRequestForm';

export default function ProformaAppForm({app, name, router}) {
  const classes = app.getStyles();
  const formName = name;
  const ref = useRef();
  const [state, setState] = useState({});
  const control = app.createControl(ref,state,formName);  
  
  const [statusProvider, setStatusProvider] = useState([]);

  useEffect(()=>{
    var isSubscribed = true;
    app.registerForm(formName, control);
    if(isSubscribed){
      app.service.get(
        '/bean/find/status',
        (p) => {
          if(p.success){
            setStatusProvider(p.result);
          }
        }
      );
    }
    return () => {
      isSubscribed = false;
      app.unregisterForm(formName);
    }
  },[]); 

  return (
    <Container component="main" ref={ref}>
      <Spacer/>
      <Paper elevation={1} className={classes.paper}>
        <Typography component="h1" variant="h5">
          Your Content
        </Typography>
        <Spacer/>
        <SimpleForm
          name={formName}
          formKey="proformalist-form"
          control={control}
          app={app}
          mapping={{
            schema: (rule) => {
              return {
                status: rule.string()
              }
            },
            fields: [
            ],
            onSubmit: (values, formCtrl) => {
              if(formCtrl.isValid()){
                app.getForm(formName).findTable('proformalist-grid').refresh();
              }
              formCtrl.setSubmitting(false);
            },
            buttons: [
              {label:'Search', variant:'contained', color:'primary', type:'submit', grid:{sm:2}},
              {label:'Create', variant:'contained', color:'primary', grid:{sm:2}, onClick:(action, formCtrl)=>{
                app.side(
                  {
                    onRender: ()=>{ return <ProformaRequestForm name="app.proforma.ProformaRequestForm" app={app} router={router} mode="create" providers={{statusProvider}}/> },
                    onClose: ()=>{
                      app.getForm(formName).findTable('proformalist-grid').refresh();
                    }
                  }
                );
              }}]              
          }}          
      ></SimpleForm>      
      </Paper>
      <Spacer/>
      <SimpleTable 
        app={app}
        name={formName}
        control={control}
        tableKey="proformalist-grid"
        columns={
          [
            { title: "Date Created", field: "requestedon"},
          ]
        }
        options={{
          padding:'dense',
          sorting: true,
          pageSize: 5,
          pageSizeOptions: [5,10,25],
          paging: true,
          search: false
        }}
        data={
          query =>
            app.service.promise((resolve,reject)=>{
              console.log('loading data');
              var values = app.getForm(formName).getSearchValues();
              var data = app.service.prepQuery(query, values);
              app.service.post('/bean/search/proforma',data,(p)=>{
                if(p.success){
                  resolve(app.service.prepResult(query, p));
                }else{
                  reject();
                }
              });
            })
        }
        actions={[
          {
            icon: app.icons.Edit,
            tooltip: 'Edit',
            onClick: (event, rowData) => {
              app.side(
                {
                  onRender: ()=>{ 
                    var data = {...rowData}; 
                    console.log(data);
                    delete data.tableData; 
                    return <ProformaRequestForm name="app.proforma.ProformaRequestForm" app={app} router={router} mode="update" rowData={data} providers={{statusProvider}}/> 
                  },
                  onClose: ()=>{
                    console.log('refreshing');
                    app.getForm(formName).findTable('proformalist-grid').refresh();
                  }
                }
              );                            
            }
          },
          {
            icon: app.icons.Assignment,
            tooltip: 'Packinglist File',
            onClick: (event, rowData) => {
              app.service.download(rowData.packinglistfile);
            }
          },        
          {
            icon: app.icons.ViewList,
            tooltip: 'Processed Packinglist File',
            onClick: (event, rowData) => {
              app.service.download('/bean/extract/packinglist?data='+JSON.stringify({'proforma_id':rowData.id}));
            }
          },
          {
            icon: app.icons.ViewList,
            tooltip: 'Packinglist Report',
            onClick: (event, rowData) => {
              app.service.download('/bean/export/proformareport?data='+JSON.stringify({'proforma_id':rowData.id}));
            }
          },           
          {
            icon: app.icons.Delete,
            tooltip: 'Delete',
            onClick: (event, rowData) => {
              app.ask("You want to delete " + rowData.name, (reply)=>{
                if(reply=='yes'){
                  app.service.get('/bean/remove/proforma/'+rowData.id,(p)=>{
                    if(p.success){
                      app.notice.warning(p.message);
                      app.getForm(formName).findTable('proformalist-grid').refresh();
                    }else{
                      app.alert(p.message);
                    }
                  });                  
                }
              });
            }
          }
        ]}        
        onRowClick={(event, rowData, togglePanel) => {
          /*var formCtrl = app.getForm(formName);
          formCtrl.setValues(rowData);
          formCtrl.validate();*/
        }}
      />
    <Spacer/>
    </Container>
  );
}