import React, { useEffect } from 'react';

import LoginForm from '~app-src/module/core/LoginForm';
import HomeForm from '~app-src/module/core/HomeForm';

import ProformaAppForm from '~app-src/module/proforma/ProformaAppForm';

export default class CoreRouter{
    static sharedInstance = CoreRouter.sharedInstance == null ? new CoreRouter() : this.sharedInstance;

    componentList = [
        {
            'path':'/',
            'component':(app, router) => {
                return <HomeForm name="app.core.HomeForm" app={app} router={router}/>
            }
        },
        {
            'path':'/proforma/list',
            'component':(app, router) => {
                return <ProformaAppForm name="app.proforma.ProformaAppForm" app={app} router={router}/>
            }
        }
    ];

    componentMap = {};

    constructor(){
        this.componentList.map((field, index) => {
            this.componentMap[field.path] = field;
        });
    }

    getComponent(isAuthenticated, field, app, router){
        if(isAuthenticated){
            return field.component(app, router);
        }else{
            return <LoginForm name="app.core.LoginForm" app={app} router={router} />;
        }
    }

    getPaths(){
        return this.componentList;
    }

    static getInstance(){
        return CoreRouter.sharedInstance;
    }

}