import React, {useState, useEffect} from 'react';
import useStateWithCallback from 'use-state-with-callback';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';

import ClearIcon from '@material-ui/icons/Clear';
import CancelIcon from '@material-ui/icons/Cancel';

export default function SideDrawer({app}) {
    const classes = app.getStyles();

    const defaultState = {
        open: false,
        onRender: null,
        onClose: null
    };

    const [state, setState] = useStateWithCallback({...defaultState},(lastState)=>{
        app.sideHandle.lastState = lastState;
    });

    const handleClose = () => {
        if(app.sideHandle.lastState.onClose) app.sideHandle.lastState.onClose();
        setState({...defaultState});
    }

    useEffect(()=>{
        app.setSideHandle({
            state:state,
            lastState:state,
            setState:setState,
            close:handleClose
        });      
    },[]);    

    return <div>
        <Drawer anchor="right" open={state.open} onClose={handleClose}>
            {state.onRender && state.onRender()}            
        </Drawer>
    </div>;
}
