import React, {useState, useEffect} from 'react';
import useStateWithCallback from 'use-state-with-callback';
import MaskedInput from 'react-text-mask';
import {
  LinearProgress,
  MenuItem,
  TextField,
  Input,
  Grid,
  Button,
  FormControl,
  Divider,
  InputLabel,
  FormGroup,
  FormControlLabel,
  FormHelperText,
  Switch,
  Checkbox,
  InputAdornment,
  IconButton,
  Box,
  Typography
} from '@material-ui/core';

import SearchIcon from '@material-ui/icons/Search';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import ToggleOffIcon from '@material-ui/icons/ToggleOff';
import ToggleOnIcon from '@material-ui/icons/ToggleOn';
import InfoIcon from '@material-ui/icons/Info';
import { KeyboardDatePicker } from "@material-ui/pickers";

import * as Yup from 'yup';

export default function SimpleForm({app, name, formKey, control, mapping, state, setState}) {
    const classes = app.getStyles();
    const formBag = {
        name:name,
        formKey:(formKey) ? formKey: app.makeKey(),
        formRef:undefined,
        initConfig:{}
    };

    const [rendered, setRendered] = useState(false);
    const [refresh, setRefresh] = useStateWithCallback(true,(lastRefresh)=>{
        if(lastRefresh==false){
            setRefresh(true);
        }
    });
    
    const [isSubmitting, setSubmitting] = useState(false); 
    const [popoverTarget, setPopoverTarget] = useState('');
    const [popoverCancel, setPopoverCancel] = useState(false);

    const init = (type) => {
        if(formBag.initConfig[type]){
            return formBag.initConfig[type];
        }

        formBag.initConfig['errors'] = {};
        formBag.initConfig['cacheDPData'] = {};
        formBag.initConfig['checkDPData'] = {};
        formBag.initConfig['selectedData'] = {};
        formBag.initConfig['values'] = {};
        formBag.initConfig['visibles'] = {};
        formBag.initConfig['dataMap'] = {};
        formBag.initConfig['disabled'] = {};
        formBag.initConfig['files'] = {};

        for(var i=0;i<mapping.fields.length;i++){
            var field = mapping.fields[i];
            formBag.initConfig['errors'][field.name] = '';
            formBag.initConfig['disabled'][field.name] = (field.disabled) ? true:false;
            formBag.initConfig['visibles'][field.name] = (field.visible==false) ? false:true;
            if(field.cmp==='search'){
                formBag.initConfig['cacheDPData'][field.name] = field;
                formBag.initConfig['selectedData'][field.name] = [];
            }else if(field.cmp==='checkbox' || field.cmp==='toggle'){
                formBag.initConfig['checkDPData'][field.name] = field.dataProvider;
            }else if(field.cmp=='fileupload'){
                formBag.initConfig['files'][field.name] = field.name+'-file';
            }
            if(state){
                if(!state[field.name]) state[field.name] = (field.value) ? field.value:'';
            }else{
                formBag.initConfig['values'][field.name] = (field.value) ? field.value:'';
            }
            if(field.dataProvider && field.dataProvider.length>0 && field.dataProvider[0].id){
                formBag.initConfig['dataMap'][field.name] = {};
                for(var j=0;j<field.dataProvider.length;j++){
                    var option = field.dataProvider[j];
                    formBag.initConfig['dataMap'][field.name][option.id] = option.name;
                }
            }            
        }
        if(state) formBag.initConfig['values'] = state;
        return formBag.initConfig[type];
    }

    const [files, setFiles] = useStateWithCallback(()=>{
        return init('files');
    },(lastFiles)=>{
        if(rendered){
            app.getForm(name).cacheFormFiles(formBag.formKey, lastFiles);
        }
    });

    const [visible, setVisible] = useStateWithCallback(()=>{
        return init('visibles');
    },(lastVisible)=>{
        if(rendered){
            app.getForm(name).cacheFormVisibles(formBag.formKey, lastVisible);
        }
    });

    const [disabled, setDisabled] = useStateWithCallback(()=>{
        return init('disabled');
    },(lastDisabled)=>{
        if(rendered){
            app.getForm(name).cacheFormDisabled(formBag.formKey, lastDisabled);
        }
    }); 

    const [errors, setErrors] = useStateWithCallback(()=>{
        return init('errors');
    },(lastErrors)=>{
        if(rendered){
            app.getForm(name).cacheFormErrors(formBag.formKey, lastErrors);
        }
    }); 

    const [cacheDPData, setCacheDPData] = useState(()=>{
        return init('cacheDPData');
    });

    const [checkDPData, setCheckDPData] = useState(()=>{
        return init('checkDPData');
    });    

    const [selectedData, setSelectedData] = useStateWithCallback(()=>{
        return init('selectedData');
    },(lastData)=>{
        if(rendered){
            app.getForm(name).cacheSelectedData(formBag.formKey, lastData);
        }
    });  

    const [values, setValues] = useStateWithCallback(() => {
        return init('values');
    },(lastValues)=>{
        if(rendered){
            app.getForm(name).cacheFormValues(formBag.formKey, lastValues);
            if(setState) setState(lastValues);
        }
    }); 

    const [dataMap, setDataMap] = useState(() => {
        return init('dataMap');
    });    

    if(mapping.schema){
        formBag.schema = Yup.object().shape(mapping.schema(Yup));
    }else{
        formBag.schema = Yup.object().shape({});
    }
    formBag.validateField = (fieldName, event) => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        var formErrors = app.getForm(name).getErrors(formBag.formKey);
        try{
            formBag.schema.validateSyncAt(fieldName, {[fieldName]:formValues[fieldName]});
            setErrors({...formErrors, [fieldName]:''});
        }catch(e){
            setErrors({...formErrors, [fieldName]:e.message});
        }
    };
    formBag.validate = (event) => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        var formErrors = app.getForm(name).getErrors(formBag.formKey);
        for(var i in formValues){
            try{
                formBag.schema.validateSyncAt(i, {[i]:formValues[i]});
                formErrors[i] = '';
            }catch(e){
                if(e.message.indexOf('The schema does not contain')==-1){
                    formErrors[i] = e.message;
                }
            }
        }
        setErrors({...formErrors});
    };    
    formBag.isValid = (skipCascade) => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        var valid = formBag.schema.isValidSync(formValues);
        if(!skipCascade){
            try{
                formBag.validate();
            }catch(e){
                console.log('errors',e);
            }
        }
        return valid;
    };
    formBag.getFormData = (data) => {
        var formData = new FormData();
        var json = JSON.stringify(data);
        formData.append('data', json);
        for(var i in files){
            var file = formBag.findRawField(files[i]);
            if(file.files.length>0)
                formData.append(i, file.files[0]);
        }
        return formData;
    }
    formBag.isSubmitting = isSubmitting;
    formBag.setSubmitting = setSubmitting;
    formBag.setDataMap = setDataMap;
    formBag.dataMap = dataMap;

    const handleSearchFocus = (field) => event => {
        /*if(values[field.name]=='' && popoverTarget=='' && app.popSelectHandle && !app.popSelectHandle.open){
            handleOnPopoverOpen(field, event);
            setErrors({...errors, [field.name]:''});
        }else{
            if(app.popSelectHandle && app.popSelectHandle.handleClose){
                app.popSelectHandle.handleClose();
            }
        }*/
        setErrors({...errors, [field.name]:''});
    };

    const handleOnPopoverOpen = (field, event) => {
        if(!popoverCancel){
            setPopoverTarget(field.name); 
            setErrors({...errors, [field.name]:''}); 
            app.pop(
                'lookup', 
                field.name, 
                field.label,
                values[field.name], 
                field.dataProvider, 
                setPopoverTarget,
                setPopoverCancel,
                selectData,
                field.multiple,
                dataMap[field.name],
                event.target
            );
        }
    };

    const handleFileUploadChange = (fieldName) => event => {
        if(event.target.value && values[fieldName]==''){
            setValue(fieldName,event.target.value);
        }
    };

    const handleCheckBoxClick = (fieldName) => event => {
        console.log(fieldName,event,event.target.checked);
        if(isDisabled(fieldName)) return;
        if(values[fieldName]=='') values[fieldName] = checkDPData[fieldName][0].id;
        if(event.target.checked){
            setValue(fieldName,checkDPData[fieldName][1].id);
        }else{
            setValue(fieldName,checkDPData[fieldName][0].id);
        }
    };

    const handleFileUploadClick = (fieldName) => event => {
        if(isDisabled(fieldName)) return;
        var hiddenFileField = formBag.findRawField(fieldName+'-file');
        hiddenFileField.click();
    };

    const handleClearErrors = (fieldName) => event => {
        setErrors({...errors, [fieldName]:''});
    };
    const handleClearAllErrors = () => {
        var emptyErrors = {};
        var localErrors = formBag.initConfig['errors'];
        for(var i in localErrors){
            emptyErrors[i] = '';
        }
        setErrors(emptyErrors);
    };
    const handleClearSomeErrors = (data) => {
        var emptyErrors = {};
        var localErrors = formBag.initConfig['errors'];
        for(var i in localErrors){
            if(data[i])
                emptyErrors[i] = '';
        }
        setErrors(emptyErrors);
    };
    const validateField = (form, fieldName, event) => {
        if(popoverTarget!=fieldName)
            form.validateField(fieldName, event);
    };
    const handleValidate = (form,fieldName) => event => {
        validateField(form, fieldName, event);
    };
    const setValue = (fieldName, value) => {
        setValues({ ...values, [fieldName]: value });
    }
    const handleDateChange = (fieldName, date) => {
        console.log(fieldName, date);
        setValue(fieldName, date);
    }
    const handleChange = fieldName => event => {
        setValue(fieldName, event.target.value);
    };
    const handleSwitchChange = fieldName => event => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        setValue(fieldName, !formValues[fieldName]);
    };
    const handleSubmit = (event) => {
        if(mapping.onSubmit){
            setSubmitting(true);
            mapping.onSubmit(app.getForm(name).getFormValues(formBag.formKey), formBag);
        }
    };
    const selectData = (fieldName, records, multiple) => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        var selectedCache = app.getForm(name).getSelectedCache(formBag.formKey);
        if(multiple){
            if(records.length>0){
                setValues({ ...formValues, [fieldName]: records });
                setSelectedData({...selectedCache, [fieldName]:records});
            }else{
                setValues({ ...formValues, [fieldName]: [] });
                setSelectedData({...selectedCache, [fieldName]:[]});
            }
        }else{
            if(records.length>0){
                setValues({ ...formValues, [fieldName]: records[0].id });
                setSelectedData({...selectedCache, [fieldName]:records});
            }else{
                setValues({ ...formValues, [fieldName]: '' });
                setSelectedData({...selectedCache, [fieldName]:[]});
            }
        }
    };
    const setVisibleField = (fieldName, flag) => {
        setVisible({ ...(app.getForm(name).getVisibles(formBag.formKey)), [fieldName]: flag });
    }
    const setVisibles = (f) => {
        var data = app.getForm(name).getVisibles(formBag.formKey);
        var clr = {};
        for(var i in f){
            data[i] = f[i];
            if(f[i]==true){
                clr[i] = '';
            }
        }
        setVisible(data);
    }
    const setAllVisible = (flag) => {
        var data = app.getForm(name).getVisibles(formBag.formKey);
        for(var i in data){
            data[i] = flag;
        }
        setVisible(data);
    }
    const setDisabledField = (fieldName, flag) => {
        setDisabled({ ...(app.getForm(name).getDisabled(formBag.formKey)), [fieldName]: flag });
    }
    const setDisables = (f) => {
        var data = app.getForm(name).getDisabled(formBag.formKey);
        var clr = {};
        for(var i in f){
            data[i] = f[i];
            if(f[i]==true){
                clr[i] = '';
            }
        }
        setDisabled(data);
        handleClearSomeErrors(clr);
    }
    const setAllDisabled = (flag) => {
        var data = app.getForm(name).getDisabled(formBag.formKey);
        for(var i in data){
            data[i] = flag;
        }
        setDisabled(data);
    }
    const setExternalValue = (fieldName, value) => {
        if(cacheDPData[fieldName]){
            var field = cacheDPData[fieldName];
            if(field.multiple){
                var ids = value.split(',');
                var keys = {};
                for(var x=0;x<ids.length;x++){
                    keys[ids[x].trim()] = true;
                }
                var selectIDs = [];
                for(var i=0;i<field.dataProvider.length;i++){
                    if(keys[field.dataProvider[i].id]){
                        selectIDs.push(field.dataProvider[i].id);
                    }
                }
                return selectData(fieldName, selectIDs, true);
            }else{
                for(var i=0;i<field.dataProvider.length;i++){
                    if(field.dataProvider[i].id==value){
                        return selectData(fieldName, [field.dataProvider[i]], false);
                    }
                }
            }
        }else{        
            setValues({ ...(app.getForm(name).getValues(formBag.formKey)), [fieldName]: value });
        }
    };
    const fns = (key) => {
        return name+'-'+formKey+'-'+key;
    };

    const getError = (fieldName) => {
        if(app.getForm(name)) return app.getForm(name).getError(formBag.formKey,fieldName);
    };    
    const isDisabled = (fieldName) => {
        if(app.getForm(name)) return app.getForm(name).isDisabled(formBag.formKey,fieldName);
    };
    const isVisible = (fieldName) => {
        if(app.getForm(name)) return app.getForm(name).isVisible(formBag.formKey,fieldName);
    };
    const boxVisible = (fieldName) => {
        return visible[fieldName]==false ? classes.hiddenGridItem:{};
    };
    const colorIcon = (fieldName, color) => {
        if(isDisabled(fieldName)){
            return 'disabled';
        }else{
            return color;
        }
    };

    formBag.refresh = () => {
        setRefresh(false);
    };
    formBag.clearErrors = () => {
        handleClearAllErrors();
    };
    formBag.setVisibles = (data) => {
        setVisibles(data);
    };
    formBag.setVisible = (fieldName, flag) => {
        setVisibleField(fieldName, flag);
    };
    formBag.show = (flag) => {
        setAllVisible(flag);
    };
    formBag.setDisables = (data) => {
        setDisables(data);
    };
    formBag.setDisabled = (fieldName, flag) => {
        setDisabledField(fieldName, flag);
    };
    formBag.disable = (flag) => {
        setAllDisabled(flag);
    };
    formBag.setValue = (fieldName, value) => {
        setExternalValue(fieldName, value);
    };
    formBag.setValues = (data) => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        var selectedCache = app.getForm(name).getSelectedCache(formBag.formKey);
        
        for(var fieldName in data){
            if(formValues[fieldName]!=undefined){
                if(cacheDPData[fieldName]){
                    var field = cacheDPData[fieldName];
                    if(field.multiple){
                        var ids = data[fieldName].split(',');
                        var keys = {};
                        for(var x=0;x<ids.length;x++){
                            keys[ids[x].trim()] = true;
                        }
                        var selectIDs = [];
                        for(var i=0;i<field.dataProvider.length;i++){
                            if(keys[field.dataProvider[i].id]){
                                selectIDs.push(field.dataProvider[i].id);
                            }
                        }
                        if(selectIDs.length>0){
                            formValues[fieldName] = selectIDs;
                            selectedCache[fieldName] = selectIDs;
                        }else{
                            formValues[fieldName] = [];
                            selectedCache[fieldName] = [];
                        }
                    }else{
                        var selectedID = '';
                        var selectedRow = [];
                        for(var i=0;i<field.dataProvider.length;i++){
                            if(field.dataProvider[i].id==data[fieldName]){
                                selectedID = field.dataProvider[i].id;
                                selectedRow = [field.dataProvider[i]];
                                break;
                            }
                        }
                        formValues[fieldName] = selectedID;
                        selectedCache[fieldName] = selectedRow;
                    }
                }else{
                    formValues[fieldName] = data[fieldName];
                }
            }
        }
        setValues({ ...formValues});
        setSelectedData({...selectedCache});

    };  
    formBag.getValue = (fieldName) => {
        return app.getForm(name).getValues(formBag.formKey)[fieldName];
    };
    formBag.getValues = () => {
        return app.getForm(name).getValues(formBag.formKey);
    };
    formBag.reset = () => {
        var formValues = app.getForm(name).getValues(formBag.formKey);
        for(var i in formValues){
            if(Array.isArray(formValues[i])){
                formValues[i] = [];
            }else{
                formValues[i] = '';
            }
        }
        setValues({...formValues});
    };
    formBag.submit = () => {
        handleSubmit();
    };
    formBag.findRawField = (fieldName) => {
        if(formBag.formRef && formBag.formRef.getElementsByTagName){
            var elements = formBag.formRef.getElementsByTagName('input');
            for(var i=0;i<elements.length;i++){
                if(elements[i].name==fieldName){
                    return elements[i];
                }
            }
        }
    };

    useEffect(()=>{
        setRendered(true);
        control.addForm(formBag);
        return () => {
        }
    },[]);

    return <form ref={(el) => (el) ? formBag.formRef = el : null}onSubmit={(event)=>{ event.preventDefault(); handleSubmit(event); }} noValidate autoComplete="off">
            {refresh &&
            <Grid container spacing={2}>
                {
                    mapping.fields && mapping.fields.map((field, index) => {
                        field.className = classes[field.cls];
                        field.autoComplete = 'off'
                        if(!field.variant){
                            field.variant = 'standard';
                        }
                        if(field.fullWidth==undefined) field.fullWidth = true;
                        if(field.type=='date' || field.type=='time' || field.type=='datetime-local' || field.type=='datetime'){
                            field.InputLabelProps={
                                shrink: true
                            };
                        }
                        if(!field.grid){
                            field.grid = {xs:12};
                        }
                        if(!field.grid.xs){
                            field.grid.xs = 12;
                        }
                        if(field.cmp=='search'){
                            var selectField = {...field};
                            delete selectField.multiple;
                            delete selectField.dataProvider;

                            var selectProps = null;
                            if(field.multiple){
                                selectProps = {
                                    open:false,
                                    multiple: (field.multiple) ? true:false,
                                    IconComponent:SearchIcon,
                                    renderValue:(selected) => {
                                        var list = [];
                                        for(var i=0;i<selected.length;i++){
                                            list.push(dataMap[field.name][selected[i]]);
                                        }
                                        return list.join(', ');
                                    },
                                    onOpen:(event)=>{
                                        handleOnPopoverOpen(field, event);
                                    }                                       
                                };
                            }else{
                                selectProps = {
                                    open:false,
                                    multiple: false,
                                    IconComponent:SearchIcon,
                                    onOpen:(event)=>{
                                        handleOnPopoverOpen(field, event);
                                    }                                       
                                };
                            }

                            var searchValue = undefined;
                            if(cacheDPData[field.name]){
                                if(field.multiple){
                                    var ids = searchValue.split(',');
                                    var keys = {};
                                    for(var x=0;x<ids.length;x++){
                                        keys[ids[x].trim()] = true;
                                    }
                                    var selectIDs = [];
                                    for(var i=0;i<field.dataProvider.length;i++){
                                        if(keys[field.dataProvider[i].id]){
                                            selectIDs.push(field.dataProvider[i].id);
                                        }
                                    }
                                    if(selectIDs.length>0){
                                        searchValue = selectIDs;
                                        selectedData[field.name] = selectIDs;
                                    }else{
                                        searchValue = [];
                                        selectedData[field.name] = [];
                                    }                                    
                                }else{
                                    var selectedID = '';
                                    var selectedRow = [];
                                    for(var i=0;i<field.dataProvider.length;i++){
                                        if(field.dataProvider[i].id==values[field.name]){
                                            selectedID = field.dataProvider[i].id;
                                            selectedRow = [field.dataProvider[i]];
                                            break;
                                        }
                                    }
                                    searchValue = selectedID;
                                    selectedData[field.name] = selectedRow;
                                }
                                values[field.name] = searchValue;
                            }

                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)}
                                        {...selectField}
                                        disabled={disabled[field.name]}
                                        select
                                        value={values[field.name]}
                                        onBlur={handleValidate(formBag, field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        onFocus={handleSearchFocus(field)}
                                        InputLabelProps={{shrink: true}}
                                        SelectProps={selectProps}>
                                        {selectedData[field.name].map(option => (
                                            <MenuItem key={fns(field.name)+'-'+option.id+'-option'} selected={option.id==values[field.name]} value={option.id}>{option.name}</MenuItem>
                                        ))}
                                    </TextField>
                            </Grid>;
                        }else if(field.cmp=='select'){
                            var selectField = {...field};
                            delete selectField.dataProvider;
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)} {...selectField}
                                        disabled={disabled[field.name]}
                                        select
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        onBlur={handleValidate(formBag, field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        onFocus={handleClearErrors(field.name)}>
                                        {field.dataProvider.map(option => (
                                            <MenuItem key={fns(field.name)+'-'+option.id+'-option'} value={option.id}>{option.name}</MenuItem>
                                        ))}
                                    </TextField>
                            </Grid>;
                        }else if(field.cmp=='fileupload'){
                            var fileUploadField = {...field};
                            delete fileUploadField.accept;
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                <input id={fns(field.name)+'-file'} name={field.name+'-file'} type="file" accept={field.accept} className={classes.menuButtonHidden} onChange={handleFileUploadChange(field.name)}/>
                                    <TextField
                                        id={fns(field.name)}
                                        {...fileUploadField}
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        onBlur={handleValidate(formBag, field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        InputProps={{
                                            readOnly:true,
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                <IconButton
                                                    edge="end"
                                                    aria-label="select a file" onClick={handleFileUploadClick(field.name)} disableFocusRipple={disabled[field.name]} disabled={disabled[field.name]} disableRipple={disabled[field.name]}>
                                                    <AttachFileIcon color={colorIcon(field.name,'primary')}/>
                                                </IconButton>
                                                </InputAdornment>
                                            )
                                        }}
                                        onFocus={handleClearErrors(field.name)}
                                    />
                            </Grid>;
                        }else if(field.cmp=='checkbox'){
                            var switchField = {...field};
                            delete switchField.dataProvider;
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)}
                                        {...switchField}
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        InputProps={{
                                            readOnly:true,
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                <IconButton
                                                    edge="end"
                                                    aria-label="checkbox" onClick={handleCheckBoxClick(field.name)} disabled={disabled[field.name]} disableRipple={disabled[field.name]}>
                                                    {
                                                        values[field.name]==checkDPData[field.name][1].id ? <CheckBoxIcon color={colorIcon(field.name,'primary')}/> : <CheckBoxOutlineBlankIcon color={colorIcon(field.name,'primary')}/>
                                                    }
                                                </IconButton>
                                                </InputAdornment>
                                            )
                                        }}
                                        onFocus={handleClearErrors(field.name)}
                                    />
                            </Grid>;                               
                        }else if(field.cmp=='toggle'){
                            var switchField = {...field};
                            delete switchField.dataProvider;
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)}
                                        {...switchField}
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        InputProps={{
                                            readOnly:true,
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                    <Switch 
                                                        disabled={disabled[field.name]} 
                                                        checked={values[field.name]==checkDPData[field.name][0].id} 
                                                        onChange={handleCheckBoxClick(field.name)} 
                                                        inputProps={{ 'aria-label': 'toggle' }}
                                                        color={colorIcon(field.name,'primary')}
                                                    /> 
                                                </InputAdornment>
                                            )
                                        }}
                                        onFocus={handleClearErrors(field.name)}
                                    />
                            </Grid>;        
                        }else if(field.cmp=='datepicker'){
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <KeyboardDatePicker
                                        id={fns(field.name)}
                                        {...field}
                                        autoOk
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        InputAdornmentProps={{ position: "end" }}
                                        onChange={date => handleDateChange(field.name, date)}
                                        onFocus={handleClearErrors(field.name)}
                                    />                                
                            </Grid>;        
                        }else if(field.cmp=='hidden'){
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} style={{display:'none'}}><Input
                                type="hidden"
                                value={values[field.name]}
                                onChange={handleChange(field.name)}
                            /></Grid>;
                        }else if(field.cmp=='file' || field.cmp=='number' || field.cmp=='date' || field.cmp=='datetime-local' || field.cmp=='month' || field.cmp=='time' || field.cmp=='week'){
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)}
                                        {...field}
                                        type={field.cmp}
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        onBlur={handleValidate(formBag, field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        onFocus={handleClearErrors(field.name)}/>
                            </Grid>;
                        }else if(field.cmp=='inputmask'){
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)}
                                        {...field}
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        onBlur={handleValidate(formBag, field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputProps={{
                                            inputComponent:<MaskedInput mask={field.mask} placeholderChar={'\u2000'} showMask/>
                                        }}
                                        InputLabelProps={{shrink: true}}
                                        onFocus={handleClearErrors(field.name)}/>
                            </Grid>;
                        }else if(field.cmp=='text'){
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <Input
                                        type="hidden"
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                    />
                                    <Typography {...field}>{values[field.name]}</Typography>
                            </Grid>;                            
                        }else if(field.cmp=='button'){
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <Input
                                        type="hidden"
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                    />
                                    <Button {...field} disabled={disabled[field.name]}>{values[field.name]}</Button>
                            </Grid>;                                                      
                        }else{
                            return <Grid key={fns(field.name)+'-griditem'} item {...field.grid} className={boxVisible(field.name)}>
                                    <TextField
                                        id={fns(field.name)}
                                        {...field}
                                        disabled={disabled[field.name]}
                                        value={values[field.name]}
                                        onChange={handleChange(field.name)}
                                        onBlur={handleValidate(formBag, field.name)}
                                        error={getError(field.name)!=''}
                                        helperText={getError(field.name)}
                                        InputLabelProps={{shrink: true}}
                                        onFocus={handleClearErrors(field.name)}/>
                            </Grid>;
                        }
                    })
                }
                {formBag.isSubmitting && <Grid key={fns('line')+'-progress-griditem'} item xs={12}><LinearProgress /></Grid>}
                {
                    mapping.buttons && mapping.buttons.map((item, key) => {
                        item.className = classes[item.cls];
                        if(item.onClick){
                            var clickEvent = item.onClick;
                            item.onClick = () => {
                                clickEvent(item.label,formBag);
                            };
                        }
                        if(!item.grid){
                            item.grid = {xs:12};
                        }
                        if(!item.grid.xs){
                            item.grid.xs = 12;
                        }                        
                        if(item.fullWidth==undefined) item.fullWidth = true;

                        return <Grid key={fns(item.label)+'-button-griditem'} item {...item.grid}><Button {...item} disabled={formBag.isSubmitting}>{item.label}</Button></Grid>;
                    })
                }
        </Grid>      
        }
    </form>;

}
