import React from 'react';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import HomeIcon from '@material-ui/icons/Home';
import ListAltIcon from '@material-ui/icons/ListAlt';
import BarChartIcon from '@material-ui/icons/BarChart';


import Divider from '@material-ui/core/Divider';

export default function NavigationForm({app, router}) {

  const classes = app.getStyles();

  return (  
    <div> 
      <List>
        <ListItem button onClick={(e)=>{ router.history.push('/'); }}>
          <ListItemIcon>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText primary="Home" />
        </ListItem>        
        <Divider />        
        <ListItem button onClick={(e)=>{ router.history.push('/proforma/list'); }}>
          <ListItemIcon>
            <ListAltIcon />
          </ListItemIcon>
          <ListItemText primary="Manage Content" />
        </ListItem> 
      </List>
    </div>
  );
}