import React, { useEffect, useRef, useState } from 'react';

import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Paper from "@material-ui/core/Paper";
import Spacer from '~app-src/component/Spacer';

export default function HomeForm({app, name, router}) {
  const classes = app.getStyles();
  const formName = name;
  const ref = useRef();
  const [state, setState] = useState({});
  const control = app.createControl(ref,state,name);


  useEffect(()=>{
    app.registerForm(formName, control);
    return () => {
      app.unregisterForm(formName);
    }
  },[]); 

  return (
    <Container component="main" ref={ref}>
      <Spacer/>
      <Card className={classes.titlecard}>
        <CardContent>
          <CardMedia
            className={classes.titlecardmedia}
            image="/HerzoProforma/media/front-welcome.jpg"
            title="Proforma App"
          />
            <Typography gutterBottom variant="h5" component="h2">
              Welcome to the Custom Solutions Blitz Framework
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              A rapid application framework enabling developers to build robust systems
              with full-stack capabilities with minimal coding and complexity.
            </Typography>
        </CardContent>
        <CardActions>
          <Button size="small" color="primary" onClick={()=>{router.history.push('/proforma/list')}}>
            Start
          </Button>
        </CardActions>
      </Card>          
    </Container>
  );
}