import React, { useEffect, useRef, useState } from 'react';

import Container from '@material-ui/core/Container';

export default function PreviewFrame({app, name, router, src, height, width}) {
  const classes = app.getStyles();
  const formName = name;
  const ref = useRef();
  const [state, setState] = useState({});
  const control = app.createControl(ref,state);


  useEffect(()=>{
    app.registerForm(formName, control);
    return () => {
      app.unregisterForm(formName);
    }
  },[]); 

  return (
    <Container component="main" ref={ref}>
        <iframe src={src} height={height} width={width} className={classes.previewFullWidth}/>
    </Container>
  );
}