import React, { useState, useEffect } from 'react';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import Button from '@material-ui/core/Button';


export default function AppAlert({app, type}) {

    const [state, setState] = React.useState({
        open:false,
        title:'Blitz',
        message:'',
        type:'ok',
        callback: ()=>{}
    });

    const handleClose = (flag) => {
        if(state.callback){
            state.callback(flag);
        }
        setState({
            title:'',
            open:false,
            message:'',
            type:'ok',
            callback: ()=>{}            
        });
    }

    useEffect(()=>{
        app.setAlertHandle({
            alertState:state,
            setAppAlert:setState
        });
    },[]);

    return (
        <Dialog
        open={state.open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description">
        <DialogTitle id="alert-dialog-title">{state.title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {state.message}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          { state.type=='yesno' && <div><Button onClick={()=>{handleClose('yes')}} color="primary" variant="contained">Yes</Button> <Button onClick={()=>{handleClose('no')}} color="primary" variant="contained" autoFocus>No</Button></div> }
          { state.type=='ok' && <Button onClick={()=>{handleClose('ok')}} color="primary" variant="contained" autoFocus>Ok</Button> }
        </DialogActions>
      </Dialog>
    );

}