import React, {useState, useEffect} from 'react';
import useStateWithCallback from 'use-state-with-callback';
import Popover from '@material-ui/core/Popover';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import Chip from '@material-ui/core/Chip';
import TextField from '@material-ui/core/TextField';

import ClearIcon from '@material-ui/icons/Clear';
import BlockIcon from '@material-ui/icons/Block';
import CancelIcon from '@material-ui/icons/Cancel';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';

export default function SelectPopover({app}) {
    const classes = app.getStyles();

    const defaultState = {
        open: false,
        type: '',
        name: '',
        value: '',
        lastValue: '',
        label: '',
        selectedItem: null,
        selectedItems: null,
        multiSelect: false,
        selectedIDs: {},
        dataProvider: [],
        setPopoverTarget: null,
        setPopoverCancel: null,
        selectData: null,
        anchorEl: null,
        spacing: 20
    };

    const notSelected = {};
    const selectedIDName = {id:'popover-selected-item'};

    const [state, setState] = useStateWithCallback({...defaultState}, (lastState) => {
        app.popSelectHandle.state = lastState;
    });
    const [selectedIndex, setSelectedIndex] = useStateWithCallback(-1, (lastSelectedIndex) => {
        if(lastSelectedIndex!=-1 && document){
            var target = document.getElementById(selectedIDName.id);
            if(target && target.scrollIntoView){
                target.scrollIntoView();
            }
        }
    });

    const handleClose = () => {
        if(state.setPopoverTarget){
            state.setPopoverTarget('');
        }
        setSelectedIndex(-1);
        setState({...defaultState, open:false, setPopoverCancel: state.setPopoverCancel});
        app.delay(()=>{
            if(state.setPopoverCancel)
                state.setPopoverCancel(false);
        });
    }

    useEffect(()=>{
        app.setPopSelectHandle({
            state:{...state},
            setState:setState,
            setSelectedIndex:setSelectedIndex,
            handleClose:handleClose
        });      
    },[]);    
    
    const setValue = (value) => {
        setState({ ...state, value: value });
    }  
    const handleChange = event => {
        if(state.multiSelect){
            setValue(event.target.value.trim());
        }else{
            setValue(event.target.value);
        }
        if(event.target.value!=''){
            setSelectedIndex(0);
        }else{
            setSelectedIndex(-1);
        }
    };

    const handleSelect = event => {
        if(selectedIndex!=-1){
            handleSelectItem(state.dataProvider.filter((option, index) => {
                return (option.name.toLowerCase().indexOf(state.value.trim().toLowerCase())!=-1);
            })[selectedIndex]);
        }
    };


    const handleSubmit = event => {
        if(!state.multiSelect && selectedIndex!=-1){
            handleSelectItem(state.dataProvider.filter((option, index) => {
                return (option.name.toLowerCase().indexOf(state.value.trim().toLowerCase())!=-1);
            })[selectedIndex]);
        }        
        state.setPopoverCancel(true);
        handleClose();
    };

    const handleSelectItem = (option) => {
        if(state.multiSelect){
            if(option){
                if(state.selectedIDs[option.id]){
                    delete state.selectedIDs[option.id];
                }else{
                    state.selectedIDs[option.id] = option;
                }
                var items = [];
                var ids = [];
                for(var i in state.selectedIDs){
                    items.push(state.selectedIDs[i]);
                    ids.push(i);
                }
                setState({
                    ...state,
                    value: state.value,
                    selectedItem: option,
                    selectedItems: items,
                    selectedIDs: state.selectedIDs
                });
                state.selectData(state.name, ids, state.multiSelect);
            }
        }else{
            if(option){
                setState({
                    ...state,
                    value: option.id,
                    selectedItem: option,
                    selectedItems: null
                });
                state.selectData(state.name, [option]);
            }
            handleClose();
        }
    };

    const clearSelectedItem = () => {
        setState({
            ...state,
            value: '',
            lastValue: '',
            selectedIDs: {},
            selectedItem: null,
            selectedItems: null
        });
        setSelectedIndex(-1);
        state.selectData(state.name, [], state.multiSelect);
        state.setPopoverCancel(true);
        handleClose();
    };

    const checkSelected = (option, index) => {
        if(selectedIndex==-1){
            if(option.id==state.lastValue){
                setSelectedIndex(index);
                return true;
            }else{
                return false;
            }
        }else{
            return selectedIndex==index;
        }
    }

    const makeSelectedID = (option, index) => {
        if(checkSelected(option, index)){
            return selectedIDName;
        }else{
            return notSelected;
        }
    }    

    const getWidth = () => {
        var minWidth = {};
        if(state.anchorEl && state.anchorEl.getBoundingClientRect){
            minWidth.minWidth = state.anchorEl.getBoundingClientRect().width - (defaultState.spacing);
        }
        return minWidth
    }

    return <div>
      <Popover
        id="popover-search"
        open={state.open}
        anchorEl={state.anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >        
        <div>
            <form className={classes.paper} noValidate autoComplete="off">
            <TextField 
                id="popover-search" 
                value={state.value}
                label={state.label} 
                autoComplete="off" 
                onChange={handleChange}
                fullWidth 
                autoFocus
                onKeyDown={(e) => {
                    if(e.keyCode === 37){ //left
                    }else if(e.keyCode === 39) { //right
                    }else if(e.keyCode === 38) { //up
                        var nextStep = selectedIndex-1;
                        if(nextStep>=0){
                            setSelectedIndex(nextStep);
                        }
                    }else if(e.keyCode === 40) { //down
                        var nextStep = selectedIndex+1;
                        var length = state.dataProvider.filter((option, index) => {
                            return (option.name.toLowerCase().indexOf(state.value.trim().toLowerCase())!=-1);
                        }).length;
                        if(nextStep<length){
                            setSelectedIndex(nextStep);
                        }
                    }else if(e.keyCode === 32) { //space
                        if(state.multiSelect){
                            handleSelect(e);
                            e.preventDefault();
                        }
                    }else if(e.keyCode === 13) { //enter
                        handleSubmit(e);
                    }
                  }}
                InputProps={{
                    style: getWidth(),
                    startAdornment:<InputAdornment position="start">
                        <IconButton className={classes.lookupIconButton} aria-label="Clear Selected" onClick={clearSelectedItem}>
                            <RemoveCircleOutlineIcon />
                        </IconButton>
                    </InputAdornment>
                }}
            />
            </form>
            <List style={{maxHeight: 200, overflow: 'auto'}}>
                {
                    state.dataProvider.filter((option, index) => {
                        return (option.name.toLowerCase().indexOf(state.value.trim().toLowerCase())!=-1);
                    }).map((option, index) => {
                        return <MenuItem button key={option.id}
                            selected={checkSelected(option, index)}
                            onClick={()=>{
                                handleSelectItem(option);
                            }}>
                            {
                                state.multiSelect &&
                                <ListItemIcon>
                                <Checkbox
                                  edge="start"
                                  checked={state.selectedIDs[option.id]!=undefined}
                                  tabIndex={-1}
                                  disableRipple
                                  inputProps={{ 'aria-labelledby': option.id }}
                                />
                              </ListItemIcon>                                
                            }
                            <ListItemText {...makeSelectedID(option, index)} primary={option.name} />
                        </MenuItem>
                    })
                }                
            </List>
        </div>
        </Popover>
    </div>;
}
