import React, { useEffect, useRef, useState } from 'react';
import useStateWithCallback from 'use-state-with-callback';
import Avatar from '@material-ui/core/Avatar';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Container from '@material-ui/core/Container';

import SimpleForm from '~app-src/component/SimpleForm';
import Spacer from '~app-src/component/Spacer';

import Typography from '@material-ui/core/Typography';
import Paper from "@material-ui/core/Paper";

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

import Slide from '@material-ui/core/Slide';

export default function LoginForm({app, name, router}) {
  const classes = app.getStyles();
  const formName = name;
  const ref = useRef();
  const [enter, setEnter] = useState(false);
  const [state, setState] = useState({});
  const control = app.createControl(ref,state,formName);
  useEffect(()=>{
    let isSubscribed = true;
    app.registerForm(formName, control);
    app.delay(()=>{
      if(isSubscribed){
        setEnter(true);
      }
    },1000);
    return () => {
      isSubscribed = false;
      app.unregisterForm(formName);
    }
  },[]); 

  return (
    <Slide in={enter} direction="down">
    <Container component="main" maxWidth="xs" ref={ref}>
      <Spacer/>
      <Card className={classes.titlecard}>
        <CardContent className={classes.paperCenter}>
          <img className={classes.bigAvatar} src="/resources/pulse-logo.jpg" alt="Pulse Feed"/>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>

          <SimpleForm
            name={formName}
            formKey="login-form"
            control={control}
            app={app}
            mapping={{
              schema: (rule) => {
                return {
                  username: rule.string().required(),
                  password: rule.string().required()
                }
              },
              fields: [
                {name:'username', label:'Username', cmp:'textfield', autoFocus:true},
                {name:'password', label:'Password', cmp:'textfield', type:'password'}
              ],
              onSubmit: (values, formCtrl) => {
                if(formCtrl.isValid()){
                  app.setUser({

                  });
                  // app.service.post(
                  //   '/login',
                  //   {username:values.username, password:app.encrypt(values.password)},
                  //   (p) => {
                  //     formCtrl.setSubmitting(false);
                  //     if(p.success){
                  //       setEnter(false);
                  //       app.delay(()=>{
                  //         app.setUser(p.result.user);
                  //       },500);                        
                  //     }else{
                  //       app.alert(p.message);
                  //     }
                  //   }

                }else{
                  //formCtrl.setSubmitting(false);
                }
                formCtrl.setSubmitting(false);
              },
              buttons: [
                {type:'submit', label:'Login', variant:'contained', color:'primary'}
              ]              
            }}
            state={state}
            setState={setState}        
          ></SimpleForm>
        </CardContent>
      </Card>       
    </Container>
    </Slide>
  );
}