import React, { useEffect, useRef, useState } from 'react';
import Container from '@material-ui/core/Container';

import SimpleForm from '~app-src/component/SimpleForm';
import Spacer from '~app-src/component/Spacer';

import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import IconButton from '@material-ui/core/IconButton';
import ViewListIcon from '@material-ui/icons/ViewList';

export default function ProformaRequestForm({app, name, router, mode, rowData, providers}) {
  const classes = app.getStyles();
  const formName = name;
  const ref = useRef();
  const [state, setState] = useState((mode=='update' ? rowData : {status_id:'requested'}));
  const control = app.createControl(ref,state,formName);

  useEffect(()=>{
    console.log('rowData',rowData);
    app.registerForm(formName, control);
    return () => {
      app.unregisterForm(formName);
    }
  },[]); 

  return (
    <Container component="main" maxWidth="xs" ref={ref}>
      <Spacer/>
      <Card className={classes.titlecard}>
        <CardHeader
          action={
            <IconButton aria-label="tools" onClick={()=>{
              app.service.download('/bean/template/packinglist',{});              
            }}>
              <ViewListIcon/>
            </IconButton>
          }
          title="New Content"
        />         
        <CardContent className={classes.paperCenter}>
          <SimpleForm
            name={formName}
            formKey="request-form"
            control={control}
            app={app}
            mapping={{
              schema: (rule) => {
                return {
                  packinglistfile: rule.string().required('field is required'),
                  deliveryaddress1: rule.string(),
                  deliveryaddress2: rule.string(),
                  deliveryaddress3: rule.string(),
                  deliveryaddress4: rule.string(),
                  deliveryaddress5: rule.string(),
                  termsofdelivery: rule.string(),
                  shipper: rule.string(),
                  pickuplocation: rule.string(),
                  remarks: rule.string(),
                  status_id: rule.string()
                }
              },
              fields: [
                {name:'id', cmp:'hidden'},
                {name:'packinglistfile', label:'Select Media FIle', cmp:'fileupload'},
                {name:'packinglisthash', label:'', cmp:'hidden'},
                {name:'deliveryaddress1', label:'Title', cmp:'textfield'},
                {name:'deliveryaddress2', label:'Description', cmp:'textfield'},
                {name:'deliveryaddress3', label:'URL', cmp:'textfield'},
                {name:'status_id', label:'Status', cmp:'search', dataProvider:providers.statusProvider, disabled:true}
              ],
              onSubmit: (values, formCtrl) => {
                console.log(formCtrl.isValid());
                formCtrl.setSubmitting(true);
                var vo = app.getForm(formName).getFormValues();
                vo.requestedon =  app.moment(new Date()).format('YYYY-MM-DD');
                var data = vo;
                if(formCtrl.isValid()){
                  if(mode=='update'){
                    data = app.getForm(formName).getFormData(vo);
                    app.service.upload(
                      '/bean/update/proforma',
                      data,
                      (p) => {
                        formCtrl.setSubmitting(false);
                        if(p.success){
                          app.notice.info(p.message);
                          app.sideHandle.close();
                        }else{
                          app.alert(p.message);
                        }
                      }
                    );
                  }else if(mode=='create'){
                    delete vo.id;
                    data = app.getForm(formName).getFormData(vo);
                    app.service.upload(
                      '/bean/create/proforma',
                      data,
                      (p) => {
                        formCtrl.setSubmitting(false);
                        if(p.success){
                          app.notice.info(p.message);
                          app.sideHandle.close();
                        }else{
                          app.alert(p.message);
                        }
                      }
                    );
                  }else{
                    app.sideHandle.close();
                  }
                }else{
                  formCtrl.setSubmitting(false);
                }
              },
              buttons: [
                {type:'submit', label:'Save', variant:'contained', color:'primary'},

              ]              
            }}
            state={state}
            setState={setState}        
          ></SimpleForm>
        </CardContent>
      </Card>       
    </Container>
  );
}